:root {
  --background: #000000;
  --foreground: #ffffff;
  --font-size: 18px;
  --font-weight: 400;
  --line-height: 1.6;
  --color-white: #fff;
  --color-dark: #111;
  --color-text: #b7b7b7;
  --color-primary: #364a15;
  --color-secondary: #24d5b4;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  list-style: none;
  word-wrap: break-word;
}
html {
  scroll-behavior: smooth;
}
body {
  background: var(--background);
  color: var(--foreground);
  overflow-x: hidden;
  font-family: var(--font-poppins), 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
}
main {
  width: 100%;
}
p {
  font-family: var(--font-roboto), 'Roboto', sans-serif;
  font-size: 15px;
  font-weight: 400;
  color: #888;
  line-height: 2;
  margin: 0;
}
.font-poppins {
  font-family: var(--font-poppins), 'Poppins', sans-serif;
}
.font-roboto {
  font-family: var(--font-roboto), 'Roboto', sans-serif;
}
.font-playfair {
  font-family: var(--font-playfair), 'Playfair Display', serif;
}
@font-face {
  font-family: 'ARBONNIE';
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url('/fonts/ARBONNIE.ttf') format('truetype');
}
.font-arbonnie {
  font-family: 'ARBONNIE', 'Americana', 'Bellerose', cursive, sans-serif;
}
.portfolio-title {
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: rgba(255, 255, 255, 0.8);
  color: rgba(255, 255, 255, 0.3);
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  letter-spacing: 0.1em;
}
.portfolio-gallery {
  margin-top: 40px;
  position: relative;
}
.portfolio-item-sizer {
  width: calc(33.333% - 20px);
  visibility: hidden;
  height: 0;
}
@media (max-width: 1024px) {
  .portfolio-item-sizer {
    width: calc(50% - 20px);
  }
}
@media (max-width: 768px) {
  .portfolio-item-sizer {
    width: 100%;
  }
}
.portfolio-gallery .portfolio-item {
  margin-top: 40px;
  padding: 0 20px;
  width: calc(33.333% - 20px);
  opacity: 1;
  transform: scale(1);
  transition: opacity 0.5s, transform 0.5s;
}
.portfolio-gallery .portfolio-item.isotope-hidden {
  opacity: 0;
  transform: scale(0.2);
  pointer-events: none;
}
@media (max-width: 1024px) {
  .portfolio-gallery .portfolio-item {
    width: calc(50% - 20px);
  }
}
@media (max-width: 768px) {
  .portfolio-gallery .portfolio-item {
    width: 100%;
    padding: 0 10px;
    margin-top: 20px;
  }
}
.portfolio-gallery .item-img {
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}
.portfolio-gallery .item-img > a {
  display: block;
}
.portfolio-gallery .portfolio-image {
  width: 100%;
  height: auto;
  display: block;
  transition: all 0.5s;
}
.portfolio-gallery .item-img:hover .portfolio-image {
  filter: blur(2px);
  -webkit-filter: blur(2px);
}
.portfolio-gallery .item-img-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(38, 38, 40, 0.5);
  text-align: center;
  transition: all 0.5s;
  opacity: 0;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.portfolio-gallery .item-img:hover .item-img-overlay {
  opacity: 1;
  visibility: visible;
}
.portfolio-gallery .overlay-info {
  opacity: 0;
  transition: opacity 0.5s;
}
.portfolio-gallery .item-img:hover .overlay-info {
  opacity: 1;
}
.portfolio-gallery .overlay-info h5 {
  color: #fff;
  font-size: 22px;
  font-weight: 600;
  text-transform: lowercase;
  font-family: var(--font-poppins), 'Poppins', sans-serif;
}
.portfolio-gallery .overlay-info .char {
  display: inline-block;
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.3s cubic-bezier(0.3, 0, 0.7, 1);
}
.portfolio-gallery .item-img:hover .overlay-info .char {
  opacity: 1;
  transform: translateY(0);
  animation: fadeInUp2 0.3s cubic-bezier(0.3, 0, 0.7, 1) both;
  animation-delay: calc(20ms * var(--char-index));
}
@keyframes fadeInUp2 {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@-webkit-keyframes fadeInUp2 {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
.portfolio-item.star-birth {
  opacity: 0;
  transform: scale(0) rotate(0deg);
  animation: starBirth 2s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
}
@keyframes starBirth {
  0% {
    opacity: 0;
    transform: scale(0) rotate(0deg);
    filter: brightness(0) blur(15px) drop-shadow(0 0 0px rgba(255, 255, 255, 0));
  }
  20% {
    opacity: 0.3;
    transform: scale(0.3) rotate(72deg);
    filter: brightness(0.5) blur(10px) drop-shadow(0 0 5px rgba(255, 255, 255, 0.3));
  }
  40% {
    opacity: 0.6;
    transform: scale(0.7) rotate(144deg);
    filter: brightness(1.5) blur(5px) drop-shadow(0 0 15px rgba(255, 255, 255, 0.6));
  }
  60% {
    opacity: 0.9;
    transform: scale(1.05) rotate(216deg);
    filter: brightness(2) blur(2px) drop-shadow(0 0 25px rgba(255, 255, 255, 0.8));
  }
  80% {
    opacity: 1;
    transform: scale(0.98) rotate(288deg);
    filter: brightness(1.5) blur(1px) drop-shadow(0 0 20px rgba(255, 255, 255, 0.5));
  }
  100% {
    opacity: 1;
    transform: scale(1) rotate(360deg);
    filter: brightness(1) blur(0px) drop-shadow(0 0 10px rgba(255, 255, 255, 0.3));
  }
}
.portfolio-item {
  position: relative;
}
.portfolio-item::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0;
  height: 0;
  border-radius: 50%;
  background: radial-gradient(
    circle,
    rgba(138, 43, 226, 0.4) 0%,
    rgba(75, 0, 130, 0.3) 30%,
    rgba(255, 255, 255, 0.2) 50%,
    transparent 100%
  );
  pointer-events: none;
  z-index: -1;
  transition: width 0.6s ease, height 0.6s ease, opacity 0.6s ease;
  opacity: 0;
}
.portfolio-item.star-birth::before {
  animation: starGlow 2s ease-out forwards;
}
@keyframes starGlow {
  0% {
    width: 0;
    height: 0;
    opacity: 1;
  }
  30% {
    width: 150px;
    height: 150px;
    opacity: 0.9;
  }
  60% {
    width: 250px;
    height: 250px;
    opacity: 0.6;
  }
  100% {
    width: 400px;
    height: 400px;
    opacity: 0;
  }
}
.portfolio-item::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background: radial-gradient(
    ellipse at center,
    rgba(138, 43, 226, 0.1) 0%,
    transparent 70%
  );
  pointer-events: none;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.5s ease;
}
.portfolio-item:hover::after {
  opacity: 1;
}
.portfolio-gallery .item-img:hover {
  filter: drop-shadow(0 0 30px rgba(138, 43, 226, 0.6)) drop-shadow(0 0 15px rgba(255, 255, 255, 0.4));
  transform: scale(1.02);
  transition: transform 0.3s ease, filter 0.3s ease;
}
.portfolio-gallery .item-img:hover .portfolio-image {
  filter: blur(2px) brightness(1.3) saturate(1.2);
  -webkit-filter: blur(2px) brightness(1.3) saturate(1.2);
}
@media (max-width: 768px) {
  .portfolio-gallery .portfolio-item {
    padding: 0 10px;
    margin-top: 20px;
  }
  .portfolio-gallery .overlay-info h5 {
    font-size: 18px;
  }
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: #000;
}
::-webkit-scrollbar-thumb {
  background: #333;
  border-radius: 4px;
